import React from 'react';
import { Box, Grid, Typography, Button, CardMedia } from '@mui/material';

const HomePageSection = () => {
  return (
    <Box sx={{ backgroundColor: '#000', padding: '50px 20px', borderRadius: '8px' }}>
      <Grid container spacing={4} alignItems="center">
        
        <Grid item xs={12} md={6} sx={{ color: '#fff' }}>
          <Typography variant="overline" sx={{ color: '#00FF00' }}>Categories</Typography>
          <Typography variant="h3" sx={{ fontWeight: 'bold', marginBottom: '20px' }}>
            Enhance Your Music Experience
          </Typography>
          
          
          <Box display="flex" gap={2} mb={2}>
            <Box textAlign="center">
              <Typography variant="h4">23</Typography>
              <Typography>Hours</Typography>
            </Box>
            <Box textAlign="center">
              <Typography variant="h4">05</Typography>
              <Typography>Days</Typography>
            </Box>
            <Box textAlign="center">
              <Typography variant="h4">59</Typography>
              <Typography>Minutes</Typography>
            </Box>
            <Box textAlign="center">
              <Typography variant="h4">35</Typography>
              <Typography>Seconds</Typography>
            </Box>
          </Box>

          
          <Button variant="contained" color="success" size="large">
            Buy Now!
          </Button>
        </Grid>

        
        <Grid item xs={12} md={6}>
          <CardMedia
            component="img"
            image="data:image/jpeg;base64," 
            alt="JBL Speaker"
            sx={{ maxWidth: '100%', borderRadius: '10px' }}
          />
        </Grid>
      </Grid>
    </Box>
  );
};

export default HomePageSection;
